import React, { useEffect } from "react";
import "./forgotpassw.scss";
import { Button, Input, Form } from "antd";
import { useState } from "react";
import { FogotPasswordRequest } from "../../../services/models";
import UserAPI from "../../../services/APIBEELAND/User";
import { useHistory } from "react-router-dom";

export const Forgotpassw = () => {

  const history = useHistory();
  const [errorMessage, setErrorMessage] = useState("");
  const [successMessage, setSuccessMessage] = useState("");
  const [loading, setLoading] = useState(false);

  const onFinish = (data: FogotPasswordRequest) => {
    if (!loading) {
      setLoading(true);
      UserAPI.fogotPassword(data)
        .then(res => {
          if (res.data && res.data.status === 2000) {
            setSuccessMessage(res.data.message);
          } else {
            setErrorMessage(res.data.message);
          }
        })
        .catch(err => {
          setErrorMessage('C li xy ra, vui lng th li!');
          console.log(err)
        }).finally(() => {
          setLoading(false);
        })
    }
  }

  useEffect(() => {
    if(localStorage.getItem("token")){
      history.push('/')
    }
  }, []);

  return (
    <div className="background">
      <div className="forgot-password-container">
        <div className="content">
          <div className="title">QUN MT KHU</div>
          <div className="title-line-break" />
          <div className="note">
            Vui lng nhp a ch email Qu khch  ng k  t li mt
            khu.
          </div>

          <Form className="body"
            onFinish={onFinish}>
            <Form.Item
              name="email"
              rules={[{ required: true, message: 'Vui lng nhp a ch email ca bn!' }]}>
              <Input
                placeholder="Email"
                prefix={
                  <i style={{ fontSize: 12 }} className="fas fa-envelope" />
                }
              />
            </Form.Item>

            <Form.Item
              name="phone"
              rules={[{ required: true, message: 'Vui lng nhp s in thoi ca bn!' }]}>
              <Input
                placeholder="S in thoi"
                prefix={<i style={{ fontSize: 12 }} className="fas fa-phone" />}
              />
            </Form.Item>
            <div className="footer">
              <div className="forgot-password-error-message">
                {errorMessage}
              </div>
              <div className="forgot-password-succes-message">
                {successMessage}
              </div>
              <Button style={{ width: 168 }} className="primary-btn" htmlType="submit" loading={loading}>GI</Button>
            </div>
          </Form>
        </div>
      </div>
    </div>
  );
};
