import "./register.scss";
import { Input, Checkbox, Button, Form } from "antd";
import { RegisterRequest } from "../../../services/models";
import { useEffect, useState } from "react";
import UserAPI from "../../../services/APIBEELAND/User";
import { useHistory } from "react-router-dom";
// import ButtonCustom from "../../../components/buttonCustom/buttonCustom";

export const Register = (props: any) => {
  const history = useHistory();

  const [loading, setLoading] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");
  const [successMessage, setSuccessMessage] = useState("");

  const onFinish = (data: RegisterRequest) => {
    setErrorMessage('');
    setSuccessMessage('');
    setLoading(true);
    UserAPI.register(data)
      .then(res => {
        setLoading(false);
        if (res.data && res.data.status === 2000) {
          setSuccessMessage(res.data.message);
        } else {
          setErrorMessage(res.data.message);
        }
      })
      .catch(err => {
        setLoading(false);
        setErrorMessage("C li xy ra, vui lng th li!");
        console.log(err);
      })
  }

  useEffect(() => {
    if(localStorage.getItem("token")){
      history.push('/')
    }
  }, []);

  return (
    <div className="background">
      <div className="register-container">
        <div className="content">
          <div className="title">NG K</div>
          
          <div className="title-line-break" />
          {!loading ? <div className="loading-frame">
            <div className="loading-frame-background"></div>
          </div> : ''}
          <Form
            className="body"
            onFinish={onFinish}>
            <Form.Item
              name="name"
              rules={[{ required: true, message: 'Vui lng nhp h tn ca bn!' }]}>
              <Input
                placeholder="H v tn"
                prefix={<i style={{ fontSize: 12 }} className="fas fa-user" />}
              />
            </Form.Item>

            <Form.Item
              name="phone"
              rules={[{ required: true, message: 'Vui lng nhp s in thoi ca bn!' }]}>
              <Input
                placeholder="S in thoi"
                prefix={<i style={{ fontSize: 12 }} className="fas fa-phone" />}
              />
            </Form.Item>

            <Form.Item
              name="email"
              rules={[{ required: true, message: 'Vui lng nhp a ch email ca bn!' }]}>
              <Input
                placeholder="Email"
                prefix={
                  <i style={{ fontSize: 12 }} className="fas fa-envelope" />
                }
              />
            </Form.Item>

            <Form.Item
              name="password"
              rules={[{ required: true, message: 'Vui lng nhp mt khu!' }]}>
              <Input.Password
                placeholder="Mt khu"
                prefix={<i style={{ fontSize: 12 }} className="fas fa-key" />}
              />
            </Form.Item>

            <Form.Item
              name="passwordRe"
              rules={[{ required: true, message: 'Vui lng nhp li mt khu!' }]}>
              <Input.Password
                placeholder="Xc nhn mt khu"
                prefix={<i style={{ fontSize: 12 }} className="fas fa-key" />}
              />
            </Form.Item>

            <Form.Item
              name="emailNotification"
              className="additional-action">
              <Checkbox checked={false}>ng k nhn thng tin, thng bo qua email</Checkbox>
            </Form.Item>

            <div className="footer">
              <div className="note">
                Cc thng tin  ng k ti khon trn y ca Qu khch s c
                s dng trong sut qu trnh giao dch BS, vui
                lng nhp y  v chnh xc.
            </div>
              <div className="register-error-message">
                {errorMessage}
              </div>
              <div className="register-success-message">
                {successMessage}
              </div>
              <div className="note"></div>
              <div style={{ display: "flex", justifyContent: "center" }}>
                <Button size="large" className="primary-btn" htmlType="submit" loading={loading}>
                  NG K
                </Button>
              </div>
              <div className="note">
                <a href="/">Hng dn ng k ti khon</a>
              </div>
              <div className="note">
                Bn  c ti khon? <a href="/">ng nhp ngay</a>
              </div>
            </div>
          </Form>
        </div>
      </div>
    </div>
  );
};
